import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

/**
 * Class that defines input for cloning item in a cart
 * @extends CpqSDKInput
 */
export class GetCartLineItemPricesInput extends CpqSDKInput {
  /**
   * Fields to include as query parameters
   */
  public fieldsToInclude: string[] = ["price", "priceDetailsFields"];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * Whether to run pricing engine
   */
  public price: boolean = true;

  /**
   * Price fields to return in the response
   * If not specified, all the price fields are returned
   */
  public priceDetailsFields: string[] = [];

  /**
   * Remove method, which is used to clone get prices for all items in cart
   */
  public remoteMethod: string = "getCartLineItemPrices";

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap = (): any => {
    let inputMap: any = {};

    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.remote.params));
    } else if (
      this.actionObj &&
      this.actionObj.rest &&
      this.actionObj.rest.params
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.rest.params));
    } else {
      this.validateParams();
      inputMap.cartId = this.cartId;
      inputMap.price = this.price;
      inputMap.priceDetailsFields = this.priceDetailsFields
        .map(field => {
          return `${encodeURIComponent(field)}`;
        })
        .join(",");
    }

    return inputMap;
  };

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath = (): string => {
    if (this.actionObj && this.actionObj.rest && this.actionObj.rest.link) {
      return this.actionObj.rest.link;
    }

    this.validateParams();

    return `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
      this.cartId
    }/price${this.getEncodedQueryString()}`;
  };

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl = (): string => {
    return "";
  };

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns
   */
  public getRequestPayload = (): any => {
    return this.getInputMap();
  };

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString = (): string => {
    return queryStringEncode(this.getInputMap(), true, this.fieldsToInclude);
  };

  /**
   * Function to validate all the required params
   */
  private validateParams = () => {
    if (!this.cartId) {
      throw new Error("GetCartLineItemPricesInput must have a `cartId`");
    }
  };
}
