import { SalesForceNamespace } from "../../../common/SalesForceNamespace";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class UpdateCartItemInput extends CpqSDKInput {
  /**
   * cartId: Order/Opportunity/Quote Id.
   */
  public cartId: string;

  /**
   * items: JSONRecord that is being updated
   */
  public items: any;

  /**
   * parentObject: JSON Record of Parent Item which has updated child line item
   */
  public parentObject?: { [key: string]: any };

  /**
   * itemObject: JSON Record of Line Item which is updated
   */
  public itemObject?: { [key: string]: any };

  /**
   * price: Pricing evaluation
   */
  public price: string = "true";

  /**
   * validate: Specifies the filter field values
   */
  public validate: string = "true";

  /**
   * Remote method in the CpqAppHandler class that updates cart items.
   */
  public remoteMethod: string = "putCartsItems";

  /**
   * Constructs the input map object containing all the query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    let inputMap: any = {};

    this.checkForRequiredParams();
    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params &&
      this.actionObj.remote.params.items
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.remote.params));
    } else if (
      this.actionObj &&
      this.actionObj.rest &&
      this.actionObj.rest.params &&
      this.actionObj.rest.params.items
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.rest.params));
    } else {
      inputMap.methodName = this.remoteMethod;
      inputMap.cartId = this.cartId;
      inputMap.items = this.items || [];
      inputMap.price = this.price;
      inputMap.validate = this.validate;
    }
    return inputMap;
  }

  /**
   * Constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   */
  public getAPIPath(): string {
    this.checkForRequiredParams();
    if (this.actionObj) {
      return this.actionObj.rest.link;
    } else {
      if (SalesForceNamespace.namespace === "") {
        throw new Error("UpdateCartItemInput missing namespace.");
      }
      return `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
        this.cartId
      }/items`;
    }
  }

  /**
   * Constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {object}
   */
  public getRequestPayload() {
    return this.getInputMap();
  }

  /**
   * Constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    return "";
  }

  /**
   * Convenience method that checks for required params.
   */
  private checkForRequiredParams() {
    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params
    ) {
      if (!this.actionObj.remote.params.cartId) {
        throw new Error("UpdateCartItemInput missing cartId.");
      }
      if (!this.actionObj.remote.params.items) {
        throw new Error("UpdateCartItemInput missing items.");
      }
    } else {
      if (!this.cartId) {
        throw new Error("UpdateCartItemInput missing cartId.");
      }
      if (!this.items) {
        throw new Error("UpdateCartItemInput missing items.");
      }
    }
  }
}
