import { SalesForceNamespace } from "../../../common";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class ApplyAdjustmentInput extends CpqSDKInput {
  public fieldsToInclude = ["adjustments", "cartId", "id"];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * Line Item Id under the cart - Required
   */
  public id: string;

  /**
   * Adjustments to be applied on the lineItem
   */
  public adjustments: any;

  /**
   * Remote method in the above Remote Class to handle get products.
   */
  public remoteMethod: string = "applyAdjustment";

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (!this.cartId && !this.actionObj) {
      throw new Error("ApplyAdjustmentInput::getInputMap() must have cartId.");
    }
    if (!this.id && !this.actionObj) {
      throw new Error("ApplyAdjustmentInput::getInputMap() must have id.");
    }

    inputMap.methodName = this.remoteMethod;
    inputMap.adjustments = this.adjustments || [];

    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField] !== undefined && this[eachField] !== null) {
        inputMap[eachField] = this[eachField];
      }
    });

    return inputMap;
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {object}
   */
  public getRequestPayload(): object {
    let requestPayload: any = {};

    if (!this.actionObj && !this.cartId) {
      throw new Error(
        "ApplyAdjustmentInput::getRequestPayload() must have cartId."
      );
    }
    if (!this.actionObj && !this.id) {
      throw new Error(
        "ApplyAdjustmentInput::getRequestPayload() must have id."
      );
    }

    requestPayload.adjustments = this.adjustments || [];
    requestPayload.cartId = this.cartId;
    requestPayload.id = this.id;

    return requestPayload;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    if (!this.cartId && !this.actionObj) {
      throw new Error("ApplyAdjustmentInput::getAPIPath() must have cartId.");
    }
    if (!this.id && !this.actionObj) {
      throw new Error("ApplyAdjustmentInput::getAPIPath() must have id.");
    }
    if (this.actionObj) {
      return this.actionObj.rest.link;
    } else {
      if (SalesForceNamespace.namespace === "") {
        throw new Error(
          "ApplyAdjustmentInput::getAPIPath() must have a namespace."
        );
      }
      return (
        `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
          this.cartId
        }/items/${this.id}/pricing` + this.getEncodedQueryString()
      );
    }
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    // Since this is NOT a GET Http Request, so no query parameter is required
    return "";
  }
}
