import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class GetListsOfValuesInput extends CpqSDKInput {
  public fieldsToInclude = [
    "listkeys",
    "cartId",
    "id",
    "fields",
    "PricingVariableCode"
  ];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id.
   * Required for fetching AdjustmentCodes only
   */
  public cartId: string;

  /**
   * Line Item Id under the cart
   * Required for fetching AdjustmentCodes only
   */
  public id: string;

  /**
   * The list of types for which to retrieve values - AdjustmentCodes,TimePlans,TimePolicies
   */
  public listkeys: string;

  /**
   * List of fields for which to retrieve adjustment codes
   */
  public fields: string[];

  /**
   * Pricing variable code associated with the price field
   */
  public PricingVariableCode: any;

  /**
   * Remote method in the above Remote Class to handle get products.
   */
  public remoteMethod: string = "getListsOfValues";

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    // If AdjustmentCodes are being retrieved
    if (this.listkeys && this.listkeys.includes("AdjustmentCodes")) {
      if (!this.cartId && !this.actionObj) {
        throw new Error(
          "GetListOfValuesInput::getInputMap() must have cartId."
        );
      }
    }

    inputMap.methodName = this.remoteMethod;

    this.fieldsToInclude.forEach(eachField => {
      if (this[eachField]) {
        inputMap[eachField] = this[eachField];
      }
    });

    if (this.fields) {
      if (this.fields.length == 0) {
        delete inputMap.fields;
      } else {
        inputMap.fields = this.fields
          .map(field => `${encodeURIComponent(field)}`)
          .join(",");
      }
    }

    return inputMap;
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(): object {
    return {};
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    // If AdjustmentCodes are being retrieved - Requires cartId and lineItemId
    if (this.listkeys && this.listkeys.includes("AdjustmentCodes")) {
      if (!this.cartId && !this.actionObj) {
        throw new Error("GetListOfValuesInput::getAPIPath() must have cartId.");
      }
    }

    if (this.actionObj) {
      return this.actionObj.rest.link;
    } else {
      if (SalesForceNamespace.namespace === "") {
        throw new Error(
          "GetListOfValuesInput::getAPIPath() must have a namespace."
        );
      }
      return (
        `${SalesForceNamespace.restAPIPrefix}/v2/listsofvalues` +
        this.getEncodedQueryString()
      );
    }
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  }
}
