import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class DeletePromotionInput extends CpqSDKInput {
  public fieldsToInclude = ["cartId", "id", "validate", "price"];

  /**
   * Cart Id.
   * Required for fetching cart data
   */
  public cartId: string;

  /**
   * Remote method in the above Remote Class to handle delete promotion.
   */
  public remoteMethod: string = "deleteAppliedPromoItems";

  /**
   * Id of promotion which need to be deleted
   */
  public id: string;

  /**
   * price: Pricing evaluation, default as true
   */
  public price: string = "true";

  /**
   * validate: Specifies the filter field values, default as true
   */
  public validate: string = "true";

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    let inputMap: any = {};

    this.checkForRequiredParams();
    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params &&
      this.actionObj.remote.params.items
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.remote.params));
    } else {
      inputMap.methodName = this.remoteMethod;
      this.fieldsToInclude.forEach(eachField => {
        if (this[eachField]) {
          inputMap[eachField] = this[eachField];
        }
      });
      if (this.fields) {
        if (this.fields.length == 0) {
          delete inputMap.fields;
        } else {
          inputMap.fields = this.fields
            .map(field => `${encodeURIComponent(field)}`)
            .join(",");
        }
      }
    }
    return inputMap;
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload() {
    //Promotion only supports Remote calls so no need to pass request payload
    return this.getInputMap();
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    //Promotion only supports Remote calls
    return "";
  }

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );
    return encodedQueryString;
  }

  /**
   * method to checks for required params.
   */
  private checkForRequiredParams() {
    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params
    ) {
      if (!this.actionObj.remote.params.cartId) {
        throw new Error("DeletePromotionInput missing cartId.");
      }
      if (!this.actionObj.remote.params.id) {
        throw new Error("DeletePromotionInput missing id.");
      }
    } else {
      if (!this.cartId) {
        throw new Error("DeletePromotionInput missing cartId.");
      }
      if (!this.id) {
        throw new Error("DeletePromotionInput missing id.");
      }
    }
  }
}
