import { UpdateItemAttributesInput } from "./UpdateItemAttributesInput";

export class UpdateItemAttributesImpl {
  /**
   * Prepare the input before invoking action
   * @param {input} UpdateItemAttributesInput
   */
  public static prepareUpdateItemAttributesInput(
    input: UpdateItemAttributesInput
  ): UpdateItemAttributesInput {
    // Any modification in input if needed to be done here
    const attributesObj = { records: [{}] };

    if (input.itemObject) {
      this.cherryPickItemObjectFields.forEach(key => {
        attributesObj.records[0][key] = input.itemObject[key];
      });

      if (
        input.actionObj &&
        input.actionObj.remote &&
        input.actionObj.remote.params.items
      ) {
        input.actionObj.remote.params.items = attributesObj;
      } else if (
        input.actionObj &&
        input.actionObj.rest &&
        input.actionObj.rest.params.items
      ) {
        input.actionObj.rest.params.items = attributesObj;
      } else {
        input.items = attributesObj;
      }
    }
    return input;
  }

  /**
   * Process the response after invoking action
   * @param {response} Response from invoking action
   */
  public static processUpdateItemAttributesResponse(response: any): any {
    // Any modification in input if needed to be done here
    return response;
  }

  private static cherryPickItemObjectFields = [
    "attributeCategories",
    "Id",
    "Product2",
    "PricebookEntry",
    "PricebookEntryId"
  ];
}
