import { CloneItemInCartInput } from "./CloneItemInCartInput";

export default class CloneItemInCartImpl {
  /**
   * Method to update input before invoking action
   * @param {input} CloneItemInCartInput
   */
  public static prepareCloneItemInCartInput = (
    input: CloneItemInCartInput
  ): CloneItemInCartInput => {
    if (input.parentObject) {
      const parentConfigObject = { records: [{}] };
      parentConfigObject.records[0] = Object.assign({}, input.parentObject);

      const deleteArrayList = [
        ...CloneItemInCartImpl.toBeDeletedItems,
        "attributeCategories"
      ];
      deleteArrayList.forEach(element => {
        delete parentConfigObject.records[0][element];
      });

      if (
        input.actionObj &&
        input.actionObj.remote &&
        input.actionObj.remote.params &&
        input.actionObj.remote.params.items
      ) {
        input.actionObj.remote.params.items[0].parentRecord = parentConfigObject;
      }
      if (
        input.actionObj &&
        input.actionObj.rest &&
        input.actionObj.rest.params &&
        input.actionObj.rest.params.items
      ) {
        input.actionObj.rest.params.items[0].parentRecord = parentConfigObject;
      }

      if (input.items) {
        input.items[0].parentRecord = parentConfigObject;
      }
    }
    return input;
  };

  /**
   * Method to update response after invoking action
   * @param {response} Response from invoke action
   */
  public static processCloneItemInCartResponse = (input: any): any => {
    return input;
  };

  private static toBeDeletedItems: string[] = [
    "Attachments",
    "actions",
    "messages",
    "childProducts",
    "lineItems",
    "productGroups"
  ];
}
