import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class DeleteItemFromCartInput extends CpqSDKInput {
  /**
   * Fields to be included in query parameters
   */
  public fieldsToInclude: string[] = [
    "hierarchy",
    "lastRecordId",
    "pagesize",
    "includeAttachment",
    "fields",
    "query"
  ];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * Line Item Id under the cart - Required
   */
  public itemId: string;

  /**
   * Hierarchy
   */
  public hierarchy?: number;

  /**
   * lastRecordId
   */
  public lastRecordId?: string;

  /**
   * pagesize, Number of records to be returned
   */
  public pagesize?: number;

  /**
   * includeAttachment, Whether product attachments are returned
   */
  public includeAttachment?: boolean;

  /**
   * query, Search text
   */
  public query?: string;

  /**
   * Remote method in the aboave Remote Class to handle delete price adjustment.
   */
  public remoteMethod: string = "deleteCartsItems";

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function that constructs the input map object containing all query parameters for API
   */
  public getInputMap = (): any => {
    const inputMap: any = {};

    this.validateParams();

    this.fieldsToInclude.forEach(field => {
      if (this[field]) {
        inputMap[field] = this[field];
      }
    });

    inputMap.cartId = this.cartId;
    inputMap.id = this.itemId;

    if (this.fields) {
      if (this.fields.length == 0) {
        delete inputMap.fields;
      } else {
        inputMap.fields = this.fields
          .map(field => `${encodeURIComponent(field)}`)
          .join(",");
      }
    }

    return inputMap;
  };

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath = (): string => {
    if (this.actionObj) {
      return this.actionObj.rest.link;
    }

    this.validateParams();

    if (SalesForceNamespace.namespace === "") {
      throw new Error(
        "DeleteItemFromCartInput::getAPIPath() must have a namespace."
      );
    }

    return `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
      this.cartId
    }/items/${this.itemId}${this.getEncodedQueryString()}`;
  };

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl = (): string => {
    // Returns empty string since anonymous rest call is not supported
    return "";
  };

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function to create request body for API POST.
   * @returns {any}
   */

  public getRequestPayload = (): any => {
    return this.getInputMap();
  };

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString = (): string => {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );
    return encodedQueryString;
  };

  /**
   * This method will validate all the params before
   * constructing input map or api path
   */
  private validateParams = () => {
    if (!this.cartId) {
      throw new Error("DeleteItemFromCartInput must have a `cartId`");
    }

    if (!this.itemId) {
      throw new Error("DeleteItemFromCartInput must have an `itemId`");
    }
  };
}
