import { Action } from "../common/Action";
import { IInvokeActionInput } from "../common/InvokeActionInput.interface";

export class GetProductsInput implements IInvokeActionInput {
  /**
   * Query Names that needs to be skipped and may require custom processing.
   * If custom processing is needed, these query names will be flattened out into URL Get parameters.
   */
  private static CustomProcessQueryNames = new Set([
    "include",
    "attributes",
    "attachment",
    "filters",
    "offset",
    "fields",
    "remoteClass",
    "remoteMethod",
    "loginUserRestPath",
    "anonymousRestUrl",
    "defaultApiNamespace",
    "apiNamespace",
    "defaultAnonymousSiteUrl",
    "anonymousSiteUrl",
    "cartId",
    "actionObj"
  ]);

  /**
   * The cart Id to be used for getting a list of products.
   * It may be the regular cart id or the anonymous cart id.
   */
  public cartId?: string = "";

  /**
   * The hierarchy level to be retrieved.
   * For only the current level of products, hierarchy will be 0.
   * For retrieving current level of products with children, hierarchy will be 1.
   */
  public hierarchy?: number;

  /**
   * When doing pagination, provide the last record ID that was previously return.
   * If last record ID is not provided, get products will start from the beginning.
   */
  public lastRecordId?: string;

  /**
   * List of nodes to indicate what to include in the product results.
   * Valid values: attributes, attachments, ineligible
   * @TODO we can use enum here.
   */
  public include?: Set<string>;

  /**
   * Filter to be used on the product result.
   * Filter must be in name and value pair where name is the SObject.fieldname and value will be used for matching.
   * eg. Product2.Name:Product1_Product2 (means Product2.Name = Product1 OR Product2)
   */
  public filters?: Map<string, string>;

  /**
   * The offset to start the product list from.
   * If we are using pagesize of 20, the offset for next page will be 21.
   */
  public offset?: number;

  /**
   * The number of top level products or offers to be returned.
   * Default is 20.
   */
  public pageSize?: number;

  /**
   * List of fields to be returned.
   * Supported Product2 and Pricebook fields.
   */
  public fields?: string[];

  /**
   * Product result will be filtered by the provided Attributes and values.
   * e.g. 991360000008eNU:32GB_64GB,991360000008eNY:RED means attribute 991360000008eNU can be 32GB or 64GB AND 991360000008eNY is RED
   */
  public attributes?: string[];

  /**
   * Search query string to search for matching products.
   * Query will be used to match Product2.name or Product2.description fields.
   */
  public query?: string;

  /**
   * Flag to indicate if validation rules should be run.
   */
  public validate: boolean;

  /**
   * Flag to indicate if pricing rules should be run.
   */
  public price: boolean;

  /**
   * Remote class to handle get products.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method in the boave Remote Class to handle get products.
   */
  public remoteMethod: string = "getCartsProducts";

  /**
   * API Namespace to be used in Rest API call.
   * e.g. h_cpq_promotion
   */
  public apiNamespace: string = "";

  /**
   * Anonymous site URL.
   * e.g. https://guidedselling102-developer-edition.na35.force.com
   */
  public anonymousSiteUrl: string = "";

  /**
   * action object.
   */
  public actionObj: Action;

  /**
   *  Add params as Set object
   */
  public setInclude(params: string[]) {
    this.include = new Set(params);
  }

  /**
   * Apply the given params value of matching in rest URL.
   * @param params - parameters to be applied in the Rest Url excluding the query string.
   */
  public getAnonymousRestUrl(params?: object) {
    const resolvedParams = {
      ...{
        anonymousSiteUrl: this.anonymousSiteUrl,
        apiNamespace: this.apiNamespace,
        cartId: this.cartId
      },
      ...params
    };
    return this.anonymousRestUrl(resolvedParams);
  }

  /**
   * Apply the given params value of matching in rest path.
   * @param params - parameters to be applied in the Rest Url excluding the query string.
   */

  public getLoginUserRestPath(params?: object) {
    const resolvedParams = {
      ...{
        apiNamespace: this.apiNamespace,
        cartId: this.cartId
      },
      ...params
    };
    return this.loginUserRestPath(resolvedParams);
  }

  /**
   *  Return the encoded query of a URI that contains the properties in this class.
   *  Undefined properties will not be included in the encoded query.
   *
   *  scheme:[//[user[:password]@]host[:port]][/path][?query][#fragment]
   */
  public urlEncodedQuery(): string {
    let encodedQuery = Object.entries(this)
      .filter(
        ([key, val]) => !GetProductsInput.CustomProcessQueryNames.has(key)
      )
      .map(([key, val]) => `${key}=${encodeURIComponent(val)}`)
      .join("&");

    // fields param
    if (this.fields) {
      const fieldsToReturn = this.fields
        .map(field => `${encodeURIComponent(field)}`)
        .join(",");
      encodedQuery += "&fields=" + fieldsToReturn;
    }

    // offset param
    if (this.offset) {
      encodedQuery += "&offsetSize=" + this.offset;
    }

    // include param
    if (this.include && this.include.has("attributes")) {
      encodedQuery += "&includeAttributes=true";
    }

    // Note that attachment is SINGULAR
    if (this.include && this.include.has("attachment")) {
      encodedQuery += "&includeAttachment=true";
    }

    if (this.include && this.include.has("ineligible")) {
      encodedQuery += "&includeIneligible=true";
    }

    return encodedQuery;
  }

  public inputMap(): object {
    const inputMap = Object.keys(this)
      .filter(key => !GetProductsInput.CustomProcessQueryNames.has(key))
      .reduce((p, c) => ({ ...p, [c]: encodeURIComponent(this[c]) }), {});

    // fields param
    if (this.fields) {
      const fieldsToReturn = this.fields
        .map(field => `${encodeURIComponent(field)}`)
        .join(",");
      Object.assign(inputMap, { fields: fieldsToReturn });
    }

    // offset param
    if (this.offset) {
      Object.assign(inputMap, { offsetSize: this.offset });
    }

    // include param
    if (this.include && this.include.has("attributes")) {
      Object.assign(inputMap, { includeAttributes: true });
    }

    if (this.include && this.include.has("attachments")) {
      Object.assign(inputMap, { includeAttachments: true });
    }

    if (this.include && this.include.has("ineligible")) {
      Object.assign(inputMap, { includeIneligible: true });
    }

    return inputMap;
  }

  /**
   * Rest URL for login user to get products.
   */
  public loginUserRestPath = urlparams => {
    const namepaceSeparater = urlparams.apiNamespace !== "" ? "/" : "";
    return `${namepaceSeparater}${urlparams.apiNamespace}/v2/cpq/carts/${
      urlparams.cartId
    }/products?`;
  };

  /**
   * Rest URL for Anonymous user to get products.
   * https://guidedselling102-developer-edition.na35.force.com/services/apexrest/h_cpq_promotion/v2/cpq/carts/801410000047w0i/products
   * https://vloc-gs-developer-edition.na59.force.com/guidedSelling/services/apexrest/nimish_cmt/v2/cpq/carts/801f4000001FI6g/products
   * https://ecommercevlocity-developer-edition.na35.force.com/services/apexrest/h_cpq_promotion/v2/cpq/carts/801410000047w0i/products
   *
   */
  public anonymousRestUrl = urlparams => {
    const namepaceSeparater = urlparams.apiNamespace !== "" ? "/" : "";
    return `${
      urlparams.anonymousSiteUrl
    }/services/apexrest${namepaceSeparater}${
      urlparams.apiNamespace
    }/v2/cpq/carts/${urlparams.cartId}/products?`;
  };
}
