import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

/**
 * Class that defines input to add promotion to the cart.
 * @extends CpqSDKInput
 */
export class AddToCartPromoInput extends CpqSDKInput {
  /**
   * cartId as part of the POST body of the API for adding promotion to the working cart.
   */
  public cartId: string = "";
  /**
   * Id of promotion which need to be added to the cart.
   */
  public promotionId: string = "";
  /**
   * Array of item ids.
   */
  public items: Array<any> = [];
  /**
   * Remote method to handle add cart promotions.
   */
  public remoteMethod: string = "postCartsPromoItems";

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */

  public getAPIPath(): string {
    if (!this.cartId && !this.actionObj) {
      throw new Error("Cart Id must needed.");
    }
    if (this.actionObj) {
      return this.actionObj.rest.link;
    } else {
      return (
        `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
          this.cartId
        }/promotions` + this.getEncodedQueryString()
      );
    }
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(): any {
    return this.getInputMap();
  }

  /**
   * Implementing Abstract method of CpqSDKInput class.
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl = (): string => {
    // Returns empty string since anonymous user is not allowed to use this sdk method
    return "";
  };

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (!this.cartId && !this.actionObj) {
      throw new Error("AddToCartPromoInput::getInputMap() must have cartId.");
    }
    if (this.promotionId && !this.actionObj) {
      throw new Error(
        "AddToCartPromoInput::getInputMap() must have promotionId."
      );
    }

    inputMap.methodName = this.remoteMethod;
    inputMap.cartId = this.cartId;
    inputMap.promotionId = this.promotionId;
    inputMap.items = this.actionObj.remote.params.items;

    return inputMap;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );
    return encodedQueryString;
  }
}
