import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

export class AddProductToCartInput extends CpqSDKInput {
  public fieldsToInclude: string[] = [
    "price",
    "validate",
    "hierarchy",
    "includeAttachment",
    "fields",
    "noResponseNeeded"
  ];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * Array of items in the format [{
   *  // Comma separated list of ids of products / child products / add-ons to be added to the cart
   *  itemId: "01uf400000HY8YGAA1,01uf400000HY8YuAAL,01uf400000HY8YkAAL",
   *  // Hierarchy of parent product (in case of add-on or child product)
   *  parentHierarchyPath: "01tf4000004ULpJAAW<01tf4000004ULpOAAW<01tf4000004ULpYAAW<01tf4000004ULpiAAG",
   *  // Id of parent item (in case of add-on or child product)
   *  parentId: "802f400000YzH8WAAV"
   * }]
   */
  public items: any[];

  /**
   * Flag to decide, whether to run pricing engine or not
   * If price = false, it will not run pricing engine.
   */
  public price: boolean = true;

  /**
   * Flag to decide, whether to run validation rules or not
   */
  public validate: boolean = true;

  /**
   * Flag to decide, whether to return response or not
   */
  public noResponseNeeded: boolean = false;

  /**
   * Product Hierarchy
   */
  public hierarchy: number;

  /**
   * Flag to indicate whether product attachments are returned
   */
  public includeAttachment: boolean = false;

  /**
   * List of fields to return in the response
   * If not provided or is empty array, all fields are returned
   */
  public fields: string[];

  /**
   * Parent record object (in case of adding add-on or child product)
   */
  public parentObject?: { [key: string]: any };

  /**
   * Remote method used to add product to cart
   */
  public remoteMethod: string = "postCartsItems";

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath = (): string => {
    if (this.actionObj) {
      return this.actionObj.rest.link;
    }

    this.validateParameters();
    return `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
      this.cartId
    }/items${this.getEncodedQueryString()}`;
  };

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload = (): any => {
    return this.getInputMap();
  };

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap = (): any => {
    let inputMap: any = {};

    if (
      this.actionObj &&
      this.actionObj.remote &&
      this.actionObj.remote.params &&
      this.actionObj.remote.params.items
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.remote.params));
    } else if (
      this.actionObj &&
      this.actionObj.rest &&
      this.actionObj.rest.params &&
      this.actionObj.rest.params.items
    ) {
      inputMap = JSON.parse(JSON.stringify(this.actionObj.rest.params));
    } else {
      this.validateParameters();
      inputMap.cartId = this.cartId;
      inputMap.items = this.items;
      this.fieldsToInclude.forEach(field => {
        if (this[field]) {
          inputMap[field] = this[field];
        }
      });

      inputMap.price = this.price;
      inputMap.validate = this.validate;
      inputMap.includeAttachment = this.includeAttachment;
      inputMap.noResponseNeeded = this.noResponseNeeded;

      if (this.fields) {
        inputMap.fields = this.fields
          .map(field => `${encodeURIComponent(field)}`)
          .join(",");
      }
    }

    if (this.ipMethod) {
      inputMap.lineItems = inputMap.items;
      delete inputMap.items;
    }
    return inputMap;
  };

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString = (): string => {
    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      this.fieldsToInclude
    );

    return encodedQueryString;
  };

  /**
   * Function to validate all the required params
   */
  private validateParameters = () => {
    if (!this.cartId) {
      throw new Error("AddProductToCartInput must have a `cartId`.");
    }
    if (!this.items) {
      throw new Error("AddProductToCartInput must have an `items` array.");
    }
    if (this.items.length === 0) {
      throw new Error(
        "AddProductToCartInput must have a non-empty `items` array."
      );
    }
    if (!this.hierarchy) {
      throw new Error("AddProductToCartInput must have a `hierarchy`.");
    }
  };
}
