import { SalesForceNamespace } from "../../../common";
import { queryStringEncode } from "../../../utils";
import { CpqSDKInput } from "../../common/CpqSDKInput";

/**
 * Class that defines input to get cart summary
 * @extends CpqSDKInput
 */
export class GetCartSummaryInput extends CpqSDKInput {
  /**
   * Fields to include as query parameters in api path
   */
  public fieldsToInclude = [
    "headerFieldSet",
    "validate",
    "price",
    "translation"
  ];

  /**
   * Cart Id (Salesforce Id).Opportunity, Quote or Order Id. - Required
   */
  public cartId: string;

  /**
   * Flag to indicate whether validation messages to be included in the response.
   */
  public validate?: string = "false";

  /**
   * Flag to indicate whether pricing engine to be run.
   */
  public price?: string = "false";

  /**
   * Flag to indicate whether translated labels to be included in the response.
   */
  public translation?: string = "true";

  /**
   * Name of a Salesforce Field Set defined for the given cart object
   * (Opportunity, Quote, Order) which identifies the fields to return in the response.
   * Omitting this parameter will return all fields
   * from the cart object except the following fields:
   * IsDeleted, CreatedDate, CreatedById, LastModifiedDate, LastModifiedById,
   * SystemModstamp, LastActivityDate, CleanStatus.
   */
  public headerFieldSet?: string = "";

  /**
   * Name of Integration Procedure (IP) to get cart summary
   * ipMethod = `Type of IP`_`Subtype of IP`
   */
  public ipMethod: string = "CPQ_GetCartSummary";

  /**
   * Remote method which is used to get cart summary
   */
  public remoteMethod: string = "getCarts";

  /**
   * Function that constructs the input map object containing all query parameters
   * for API or Integration Procedure
   * @returns {any}
   */
  public getInputMap = (): any => {
    this.validateParams();

    const inputMap: any = {};

    this.fieldsToInclude.forEach(field => {
      if (this[field]) {
        inputMap[field] = this[field];
      }
    });

    inputMap.cartId = this.cartId;

    return inputMap;
  };

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath = (): string => {
    if (this.actionObj) {
      return this.actionObj.rest.link;
    }

    this.validateParams();

    return `${SalesForceNamespace.restAPIPrefix}/v2/cpq/carts/${
      this.cartId
    }${this.getEncodedQueryString()}`;
  };

  /**
   * Function that constructs the API path for Rest call
   * @returns {string}
   */
  public getAnonymousSiteRestUrl = (): string => {
    return "";
  };

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload = (): any => {
    return this.getInputMap();
  };

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString = (): string => {
    return queryStringEncode(this.getInputMap(), true, this.fieldsToInclude);
  };

  /**
   * Function that validate `GetCartSummaryInput` object to have required params
   */
  private validateParams = () => {
    if (!this.cartId) {
      throw new Error("GetCartSummaryInput must have a `cartId`");
    }
  };
}
