import { UpdateCartItemInput } from "./UpdateCartItemInput";

export class UpdateCartItemImpl {
  /**
   * Prepare the input before invoking action
   * @param {input} UpdateCartItemInput
   */
  public static prepareUpdateCartItemInput(
    input: UpdateCartItemInput
  ): UpdateCartItemInput {
    // Any modification in input if needed to be done here
    const configUpdateObject = { records: [{}] };
    const modifiedChildItemObject = { records: [{}] };

    // Update on a LineItem that has a parent
    if (input.parentObject && input.itemObject) {
      configUpdateObject.records[0] = Object.assign({}, input.parentObject);

      this.toBeDeletedItems.forEach(element => {
        delete configUpdateObject.records[0][element];
      });

      modifiedChildItemObject.records[0] = Object.assign({}, input.itemObject);

      this.toBeDeletedItems.forEach(element => {
        delete modifiedChildItemObject.records[0][element];
      });

      configUpdateObject.records[0]["lineItems"] = modifiedChildItemObject;
    }
    // Update on the Root Product which has no parent
    else if (input.itemObject) {
      configUpdateObject.records[0] = Object.assign({}, input.itemObject);

      this.toBeDeletedItems.forEach(element => {
        delete configUpdateObject.records[0][element];
      });
    }

    if (input.itemObject) {
      if (
        input.actionObj &&
        input.actionObj.remote &&
        input.actionObj.remote.params.items
      ) {
        input.actionObj.remote.params.items = configUpdateObject;
      } else if (
        input.actionObj &&
        input.actionObj.rest &&
        input.actionObj.rest.params.items
      ) {
        input.actionObj.rest.params.items = configUpdateObject;
      } else {
        input.items = configUpdateObject;
      }
    }
    return input;
  }

  /**
   * Process the response after invoking action
   * @param {response} Response from invoking action
   */
  public static processUpdateCartItemResponse(response: any): any {
    // Any modification in input if needed to be done here
    return response;
  }

  private static toBeDeletedItems = [
    "Attachments",
    "actions",
    "messages",
    "childProducts",
    "lineItems",
    "productGroups"
  ];
}
