import { AddProductToCartInput } from "./AddProductToCartInput";

export class AddProductToCartImpl {
  /**
   * Prepare the input before invoking action
   * @param {input} AddProductToCartInput
   */

  public static prepareAddProductToCartInput(
    input: AddProductToCartInput
  ): AddProductToCartInput {
    // Any modification in input if needed to be done here
    if (input.parentObject) {
      const configAddObject = { records: [{}] };
      configAddObject.records[0] = Object.assign({}, input.parentObject);

      const deleteArrayList = [...this.toBeDeletedItems, "attributeCategories"];
      deleteArrayList.forEach(element => {
        delete configAddObject.records[0][element];
      });

      if (
        input.actionObj &&
        input.actionObj.remote &&
        input.actionObj.remote.params.items
      ) {
        input.actionObj.remote.params.items[0].parentRecord = configAddObject;
      }
      if (
        input.actionObj &&
        input.actionObj.rest &&
        input.actionObj.rest.params.items
      ) {
        input.actionObj.rest.params.items[0].parentRecord = configAddObject;
      }

      if (input.items) {
        input.items[0].parentRecord = configAddObject;
      }
    }
    if (input.actionObj) {
      input.actionObj.remote.params = Object.assign(
        input.actionObj.remote.params,
        {
          configureAndAdd: true
        }
      );
      input.actionObj.rest.params = Object.assign(input.actionObj.rest.params, {
        configureAndAdd: true
      });

      if ("noResponseNeeded" in input) {
        input.actionObj.remote.params = Object.assign(
          input.actionObj.remote.params,
          {
            noResponseNeeded: input.noResponseNeeded
          }
        );
        input.actionObj.rest.params = Object.assign(
          input.actionObj.rest.params,
          {
            noResponseNeeded: input.noResponseNeeded
          }
        );
      }
    }
    return input;
  }

  /**
   * Process the response after invoking action
   * @param {response} Response from invoking action
   */

  public static processAddProductToCartResponse(response: any): any {
    // Any modification in input if needed to be done here
    return response;
  }

  private static toBeDeletedItems = [
    "Attachments",
    "actions",
    "messages",
    "childProducts",
    "lineItems"
  ];
}
