  /**
   * Create a CPQSDKConfig object that consists of a UserContext and a DataSourceService for login user.
   * @example
   * ```typescript
   *
   * // Instantiate CPQSDKConfig for login user
   * const cpqSDKConfig = VlocitySDK.cpq.createConfigForLoginUser(salesforceUrl, sessionToken);
   *
   * // Instantiate the SDK itself
   * const cpq = VlocitySDK.cpq.getInstance(cpqSDKConfig);
   * ```
   * @param {string} salesforceUrl
   * @param {string} sessionToken
   * @return {CPQSDKConfig} Configuration object to instantiate SDK.
   */
  public static createConfigForLoginUser(
    salesforceUrl: string,
    sessionToken: string
  ): CPQSDKConfig {
    const userContext = new UserContext();
    userContext.hasLogin = true;
    const datasourceForLoginUser = new DataSource({
      sessionId: sessionToken,
      url: salesforceUrl
    });
    return {
      anonymousSiteUrl: "",
      apiNamespace: "",
      context: userContext,
      create: false,
      datasource: datasourceForLoginUser
    };
  }

  /**
   * Get an instance of this SDK.
   * @param {config} configuration on initializing this SDK.
   * @returns CPQ SDK instance.
   */
  public static getInstance(config: CPQSDKConfig): CPQ {
    // If there is no instance and no config to determine how to create the instance, throw an error.
    if (!CPQ.instance && !(config || config.context)) {
      throw new Error(
        "CPQ:getInstance: CPQ cannot be instantiated properly. Both CPQSDKConfig and CPQSDKConfig.UserContext must be provided."
      );
    }

    if (!CPQ.instance || (config && config.create)) {
      CPQ.instance = new CPQ(config);
    }
    return CPQ.instance;
  }

  /**
   * Returns configuration for anonymous users.
   * @param {string} authToken
   * @return {Object<CPQSDKConfig>}
   */
  public static createConfigForAnonymousUser(authToken: string): CPQSDKConfig {
    const userContext = new UserContext();
    userContext.hasLogin = false;
    authToken = authToken;
    const datasourceForAnonymousUser = new DataSource({
      jsforceConnection: null
    });
    return {
      anonymousSiteUrl: "",
      apiNamespace: "",
      context: userContext,
      create: false,
      datasource: datasourceForAnonymousUser
    };
  }

  /**
   * Private singleton instance of this SDK. There should always be only 1 instance.
   */
  private static instance: CPQ;
  // Placeholder result object
  private result = {
    actions: "",
    messages: "",
    records: "",
    result: ""
  };
  private datasource;
  private context: UserContext;
  private config: CPQSDKConfig;

  constructor(config: CPQSDKConfig) {
    super();
    if (config === null || config.context === null) {
      throw new Error(
        "CPQ:constructor: CPQ cannot be instantiated properly. Both CPQSDKConfig and CPQSDKConfig.UserContext must be provided."
      );
    }

    this.context = config.context;
    this.config = config;

    if (this.config.datasource) {
      this.datasource = this.config.datasource;
      return;
    }

    if (this.config.salesForceSessionId && this.config.salesForceInstanceUrl) {
      this.datasource = new DataSource({
        sessionId: this.config.salesForceSessionId,
        url: this.config.salesForceInstanceUrl
      });
      return;
    }

    this.context.hasLogin = false;

    this.datasource = new DataSource({
      sessionId: "",
      url: ""
    });
  }

