  /**
   * Clones an item in a cart
   *
   * @example
   * ```typescript
   *
   * const input = cpqSDK.createCloneItemInCartInput();
   * input.cartId = validCartId;
   * input.items = [{itemId: 'ITEM_ID_TO_CLONE'}];
   *
   * cpqSDK.cloneItemInCart(input)
   *  .then(response => {
   *    console.log('Vlocity clone item in cart call ' + response);
   *  })
   *  .catch(error => {
   *    console.log('Vlocity clone item in cart error ' + error);
   *  });
   *
   * ```
   * @param {CloneItemInCartInput} input
   * @return {Promise<Result>}
   */
  public async cloneItemInCart(input: CloneItemInCartInput): Promise<Result> {
    input = CloneItemInCartImpl.prepareCloneItemInCartInput(input);
    const response = input.ipMethod
      ? await this.invokeIntegrationProcedure(input, HttpMethod.POST)
      : await this.invokeAction(input, HttpMethod.POST);
    return CloneItemInCartImpl.processCloneItemInCartResponse(response);
  }

