  /**
   * A workaround mechanism for v102 when getProducts API do not relate attribute id to attachment url.
   * Here we assume Admin would have configured the url such that it is appended by a vertical bar followed by attribute id,
   * we would then split them apart and create a map that pairs the attribute id to the attachment url.
   * @param {Attachment[]} input
   * @return {Map<string, string>}
   */
  public createAttributeAttachmentUrlMap(
    attachments: Attachment[]
  ): Map<string, string> {
    const attributeAttachmentUrlMap = new Map();
    let urlParts: string[];
    attachments.forEach(element => {
      if (element.url) {
        urlParts = element.url.split("|");
        attributeAttachmentUrlMap.set(urlParts[0], urlParts[1]);
      }
    });
    return attributeAttachmentUrlMap;
  }

