export class CreateCartInput {
  /**
   * Query Names that needs custom processing.
   * These query names will be flattened out in URL Get parameters.
   */
  private static CustomProcessQueryNames = new Set([
    "anonymousRestUrl",
    "filters"
  ]);

  /**
   * API Namespace to be used in Rest API call.
   * e.g. h_cpq_promotion
   */
  public apiNamespace: string = "";

  /**
   * Anonymous site URL.
   * e.g. https://guidedselling102-developer-edition.na35.force.com
   */
  public anonymousSiteUrl: string = "";

  /**
   * Filter to be used to get Anonymous cartId.
   * Filter must be in name and value pair where name is either Region or Channel.  In url, the names will be paired with their respective values separated by a colon ":".
   * eg. filters=Region:CA,Channel:Web
   */
  public filters?: Map<string, string>;

  /**
   * Apply the given params value of matching in rest URL.
   * @param params - parameters to be applied in the Rest Url excluding the query string.
   */
  public getAnonymousRestUrl(params: object) {
    return this.anonymousRestUrl(params);
  }

  /**
   *  Return the encoded query of a URI that contains the properties in this class.
   *  Undefined properties will not be included in the encoded query.
   *
   *  scheme:[//[user[:password]@]host[:port]][/path][?query][#fragment]
   */
  public urlEncodedQuery(): string {
    // returns empty string if filters has not been instantiated or it is an empty map or it is a map but DOES NOT contain "Region" OR "Channel" as keys
    if (
      !this.filters ||
      this.filters.size !== 2 ||
      !this.filters.has("Region") ||
      !this.filters.has("Channel")
    ) {
      return "";
    }

    const filterEntries = Array.from(this.filters)
      .map((value, key) => `${value[0]}:${encodeURIComponent(value[1])}`)
      .join();

    return "filters=" + filterEntries + "&cartFields=Id"; // te additional query param "&cartFields=Id" is a temporary workaround due to a bug in backend API code
  }

  /**
   * Rest URL for Anonymous user to get anonymous cartId.
   * https://your-anonymous-developer-edition.na30.force.com/services/apexrest/your_namespace/v2/cpq/anoncart?filters=Region:CA,Channel:Web
   * To configure your org for the anonymous cartId, an internal confluence page is here: https://vlocity.atlassian.net/wiki/spaces/COM/pages/330891447/API+to+Create+Anonymous+quasi+Account+Cart
   */
  public anonymousRestUrl = urlparams => {
    const namepaceSeparater = urlparams.apiNamespace !== "" ? "/" : "";
    return `${
      urlparams.anonymousSiteUrl
    }/services/apexrest${namepaceSeparater}${
      urlparams.apiNamespace
    }/v2/cpq/anoncart?`;
  };
}
